<template>
	<form>
		<input type="text" placeholder="Add List" v-model="list.title">
		<button @click.prevent="addList(list, resetList())">Add List</button>
	</form>
</template>

<script>
	import {mapActions} from 'vuex';
	export default {
		data: function() {
			return {
				list: {
					title: '',
					listItems: []
				}
			}
		},
		methods: {
			...mapActions([
				'addList'
			]),
			resetList() {
				this.list = {}
			}
		},
	}
</script>


