<template>
	<div class="col-sm-12 list">
			<h1>{{$store.state.lists[($route.params.id - 1)].title}}</h1> 
			<ul class="list-group">
				<app-list-item class="list-group-item" v-for="(listItem, index) in listItems"
				:listItem="listItem" :index="index"
				>
					
				</app-list-item>
			</ul>
			<router-link tag="button" :to="'/lists/' + $route.params.id + '/edit'">Add Items</router-link>
	</div>
	
</template>

<script>
	import ListItem from "./ListItem.vue";
	import NewListItem from "./NewListItem.vue";
	import { mapGetters } from 'vuex'

	export default {
		computed: {
			listItems() {
				return this.$store.state.lists[(this.$route.params.id - 1)].listItems;
			}
		},
		components: {
			appListItem: ListItem,
			appNewListItem: NewListItem
		},
		
	}
</script>

<style lang="scss">
	.list {
		.list-group-item {

			span {
				cursor: pointer;
				&.checked {
					text-decoration: line-through;
					font-style: italic;
					color: #d0d0d0;
				}
			}

			button {
				display: inline-block;
				width: initial;
				margin: 0;
			}
		}
	}
</style>


